}

.l-header__menu {
  position: absolute;
  top: 0;
  right: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
}

@media (min-width: 900px) {
  .l-header__menu {
    top: 50%;
    right: 30px;
    transform: translate(0, -50%);
  }
}

