  display: flex;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
}

.l-header__menu .line1,
.l-header__menu .line2,
.l-header__menu .line3 {
  position: absolute;
  left: 17px;
  display: block;
  width: 22px;
  height: 2px;
  pointer-events: none;
  content: '';
  background: #000;
}

@media (min-width: 900px) {
  .l-header__menu .line1,
  .l-header__menu .line2,
  .l-header__menu .line3 {
    left: 13px;
    width: 30px;
  }
}

.l-header__menu .line1 {
  top: 0;
}

.l-header__menu .line2 {
  top: 10px;
}

.l-header__menu .line3 {
  top: 20px;
}

.l-site.-open .l-site__menu a .line1,
.l-site.-open .l-site__menu a .line2,
.l-site.-open .l-site__menu a .line3 {
  top: 18px;
}

.l-site.-open .l-site__menu a .line1 {
  transform: rotate(45deg);
}

.l-site.-open .l-site__menu a .line2 {
  display: none;
}

.l-site.-open .l-site__menu a .line3 {
  transform: rotate(-45deg);
}

.l-site.-close .l-site__menu a .line1 {
  top: 20px;
}

.l-site.-close .l-site__menu a .line2 {
  top: 27px;
}

.l-site.-close .l-site__menu a .line3 {
  top: 34px;
}

