.c-form h3 {
  margin: 0 0 12px;
}

.c-form input[type=radio] {
  display: none;
  margin: 0;
}

.c-form input[type=radio]:checked + .c-form__labelRadio:after {
  position: absolute;
  top: 50%;
  left: 6px;
  box-sizing: border-box;
  display: block;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  content: "";
  background: #000;
  border-radius: 8px;
}

.c-form__labelRadio {
  position: relative;
  display: inline-block;
  padding: 0 0 0 30px;
  margin-right: 28px;
  line-height: 1.5px;
  cursor: pointer;
}

.c-form__labelRadio:before {
  position: absolute;
  top: 50%;
  left: 0;
  box-sizing: border-box;
  display: block;
  width: 22px;
  height: 22px;
  margin-top: -11px;
  content: "";
  background-color: white;
  border: 1px solid #000;
  border-radius: 50%;
}

.c-form input[type=checkbox] {
  display: none;
  margin: 0;
}

.c-form input[type=checkbox]:checked + .c-form__labelCheck:after {
  position: absolute;
  top: 50%;
  left: 3px;
  box-sizing: border-box;
  display: block;
  width: 16px;
  height: 8px;
  margin-top: -6px;
  content: "";
  border-bottom: 3px solid #000;
  border-left: 3px solid #000;
  transform: rotate(-45deg);
}

.c-form__labelCheck {
  position: relative;
  display: inline-block;
  padding: 0 0 0 30px;
  margin-right: 28px;
  line-height: 1.5px;
  cursor: pointer;
}

.c-form__labelCheck:before {
  position: absolute;
  top: 50%;
  left: 0;
  box-sizing: border-box;
  display: block;
  width: 22px;
  height: 22px;
  margin-top: -11px;
  content: "";
  background-color: white;
  border: 1px solid #000;
}

